:root {
    --leading: 1.15;
}

body {
    font-family: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    -webkit-font-feature-settings: "clig", "salt";
    font-feature-settings: "cliga", "salt";
}

abbr {
    -webkit-font-feature-settings: "cpsp";
    font-feature-settings: "cpsp";
    /* applies proper spacing to all caps */
    &::after {
        content: " ";
        display: inline-block;
        width: .5ch;
    }
}

ul,
ol,
ul ul,
ol ul {
    padding-left: 0;
}

p:empty {
    display: none;
}

/*End Reboot Reset*/

h1 {
    font-size: 2.4rem;
    line-height: var(--leading);
    span {
        font-size: 1.5rem;
        display: block; //        line-height: var(--leading);
    }
}

h2,
h1 span {
    font-size: 1.625rem;
    line-height: var(--leading);
}

.quad-auto-nav h2,
.button h2,
.sac-state-events-section h2 {
    font-size: 1.5rem;
    line-height: var(--leading);
}

h3,
.socialbox a,
a.hex {
    font-size: 1.3rem;
    line-height: var(--leading);
    max-width: 75ch;
    clear: both;
}

h4,
.degrees a {
    font-size: 1.15rem;
    line-height: var(--leading);
    max-width: 75ch;
    clear: both;
}

p,
li {
    font-size: 1rem;
    line-height: var(--leading);
    max-width: 75ch;
}

.eventdate {
    font-size: .875rem;
}

h1,
h3,
#navbar-primary ul[role="menubar"] a,
.eventdate,
h4 {
    line-height: var(--leading);
    font-weight: 600;
}

h2,
h4 {
    line-height: var(--leading);
    font-weight: 500;
}

body,
p,
li,
h1 span {
    line-height: var(--leading);
    font-weight: 400;
}

h1 span,
[id="spotlight"] h2,
.sac-state-events-section h2,
[id="horentcomb-header"] h2 {
    font-weight: 300;
}















/* ============= Sublist Overrides ================ */

main {
    ol ul,
    :not(.t5-meetus-manual) article * ul {
        padding-left: 1em;
        list-style-type: initial;
    }
    ul ul {
        list-style-type: circle;
    }
    :not(.t5-meetus-manual) article * ol {
        list-style-type: lower-alpha;
    }
    main.t5[class*="404s"] section.links-well div ul ul {
        margin-top: 0;
    }
}


.t4.page-my-sac-state .box3 li {
    margin-bottom: 0;
}

main[class*="404"].t5 article .col-md-12 h2 {
    max-width: unset;
    margin-bottom: 1em;
}




/* =========================== BlockQuote ======================= */

.t4,
.t5 {
    blockquote {
        position: relative;
        margin-top: 2rem;
        max-width: 840px;
        display: flex;
        flex-direction: column;


        * {
            font-size: 1.125rem;
            line-height: 1.25;
            margin-bottom: 0.25rem;
        }

        p {
            font-size: 1.2rem;
            line-height: 1.4;
            padding-left: 2%;
            color: #008554;
            text-indent: -.5ch;
            &:last-child {
                font-size: 1rem;
                color: #333;
                text-align: right;
                padding-right: 5%;
                text-indent: 0;
            }
            &:last-child::before {
                display: inline;
                content: "— ";
            }
        }

        p + p {
            text-indent: 8%;
        }
    }



    @media screen and (min-width: 768px) {

        main[class*="one-column"] {
            :not(.border) {
                blockquote {
                    padding-left: 8%;
                    padding-right: 8%;
                    &::before,
                    &::after {
                        content: "“";
                        display: block;
                        position: absolute;
                        top: 0;
                        left: -1.5rem;
                        font-size: 14rem;
                        line-height: .75;
                        font-family: Georgia;
                        /* z-index: 0; */
                        color: rgba(196, 181, 129, .35);
                    }
                    &::after {
                        content: "”";
                        top: unset;
                        left: unset;
                        bottom: -2rem;
                        right: 0;
                        line-height: 0;
                    }
                }
            }
        }
    }
}
